import React, { useState, useEffect } from "react";
import {
  Gallery,
  Header,
  Placeholder,
  Banner,
  Panel,
  PanelHeader,
  Card,
  CardGrid,
  PanelHeaderBack,
  Button,
  MiniInfoCell,
  Caption,
  Subhead,
  TabsItem,
  Tabs,
  Div,
  HorizontalScroll,
  Link,
  Group,
  Footer,
  Spinner,
  Counter,
  Cell,
  FixedLayout,
  Separator,
} from "@vkontakte/vkui";
import "./Home.css";
import Icon16Chevron from "@vkontakte/icons/dist/16/chevron";
import Icon24MoneyCircle from "@vkontakte/icons/dist/24/money_circle";
import Icon24Phone from "@vkontakte/icons/dist/24/phone";
import Icon24LogoVk from "@vkontakte/icons/dist/24/logo_vk";
import Icon24Linked from "@vkontakte/icons/dist/24/linked";
import Icon20WorkOutline from "@vkontakte/icons/dist/20/work_outline";
import CartLine from "./components/CartLine";

const Category = ({
  id,
  go,
  order,
  fetchedUser,
  targetCategory,
  setActiveItem,
  setActiveSubcat,
  activeSubcat,
  setFiltredArrItem,
  filtredArrItem,
  setArrItems,
  arrItems,
  onIncrementPosition,
}) => {
  const [subcat, setSubcat] = useState(targetCategory.subcat);
  const [isLoad, setIsLoad] = useState(<Spinner />);

  useEffect(() => {
    isLoad === false && setIsLoad(<Spinner />);
    fetch("https://sahpossum.herokuapp.com/items/getVkItems", {
      method: "POST", // *GET, POST, PUT, DELETE, etc.
      mode: "cors", // no-cors, cors, *same-origin
      cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
      credentials: "same-origin", // include, *same-origin, omit
      headers: {
        "Content-Type": "application/json",
        // 'Content-Type': 'application/x-www-form-urlencoded',
      },
      redirect: "follow", // manual, *follow, error
      referrer: "no-referrer", // no-referrer, *client
      body: JSON.stringify({
        cat: targetCategory.bdName,
        subcat: activeSubcat !== null ? activeSubcat : "",
      }), // тип данны
    })
      .then((res) => res.json())
      .then((res) => {
        console.log(res);
        setArrItems(res);
        setIsLoad(false);
      })
      .catch((e) => console.log(e));
  }, []);

  return (
    <Panel id={id}>
      <PanelHeader left={<PanelHeaderBack onClick={() => go("home")} />} />
      {order !== null && <CartLine go={go} order={order} />}
      <Placeholder
        icon={
          <div
            style={{
              background: "url(" + targetCategory.icon + ") no-repeat",
              height: 50,
              width: 50,
            }}
          />
        }
      >
        Выбрана категория: {targetCategory.title}
      </Placeholder>
      <Div>
        {isLoad ? (
          isLoad
        ) : (
          <React.Fragment>
            {
              <CardGrid>
                {arrItems.map((item) => {
                  return (
                    <Card
                      key={item.id}
                      style={{
                        padding: 2,
                        borderRadius: 13,
                        margin: 0,
                        backgroundColor: "#007151",
                      }}
                      size="m"
                      mode="shadow"
                    >
                      <div
                        onClick={() => {
                          setActiveItem(item);
                          go("aboutItem");
                        }}
                        style={{
                          height: 150,
                          backgroundImage: "url(" + item.thumb_photo + ")",
                          backgroundSize: "cover",
                          backgroundPosition: "center 35%",
                          backgroundRepeat: "no-repeat",
                          borderRadius: 13,
                        }}
                      />
                      <MiniInfoCell textWrap={"short"}>
                        <Caption style={{ minHeight: 48 }} level={1}>
                          {item.title}
                        </Caption>
                      </MiniInfoCell>
                      <MiniInfoCell
                        before={<Icon24MoneyCircle height={20} width={20} />}
                      >
                        <Subhead>{item.price.text}</Subhead>
                      </MiniInfoCell>
                      <MiniInfoCell>
                        <Button
                          onClick={() => onIncrementPosition(item)}
                          size="m"
                          stretched
                          mode="outline"
                        >
                          В корзину
                        </Button>
                      </MiniInfoCell>
                    </Card>
                  );
                })}
              </CardGrid>
            }
          </React.Fragment>
        )}
      </Div>
      <Group header={<Header mode="secondary">Есть вопросы?</Header>}>
        <MiniInfoCell before={<Icon24Phone height={20} width={20} />}>
          <Link href="tel:+79022954808" target="_blank">
            +7 (902) 294-48-08
          </Link>
        </MiniInfoCell>
        <MiniInfoCell before={<Icon24LogoVk height={20} width={20} />}>
          <Link href="https://vk.com/zoomagasin" target="_blank">
            vk.com/zoomagasin
          </Link>
        </MiniInfoCell>
        <MiniInfoCell before={<Icon24Linked height={20} width={20} />}>
          <Link href="https://zoomagasin.ru/" target="_blank">
            zoomagasin.ru
          </Link>
        </MiniInfoCell>
        <Footer />
      </Group>
    </Panel>
  );
};

export default Category;
