import React, { useState, useEffect, Fragment } from "react";
import {
  Header,
  Placeholder,
  Panel,
  PanelHeader,
  PanelHeaderBack,
  Button,
  MiniInfoCell,
  Div,
  Footer,
  Counter,
  Group,
  Cell,
  Select,
  Tabs,
  TabsItem,
} from "@vkontakte/vkui";
import "./Home.css";
import bridge from "@vkontakte/vk-bridge";
import Icon56ArticleOutline from "@vkontakte/icons/dist/56/article_outline";
import Delivery from "./components/Delivery";
import CartListItems from "./components/CartListItems";

const Cart = ({
  id,
  go,
  goBack,
  typeDelivery,
  fetchedUser,
  activeCity,
  onDecrementPosition,
  onIncrementPosition,
  order,
  clearCart,
  onDeletePosition,
  activePVZ,
  setActivePVZ,
  changeCount,
}) => {
  const [deliv, setDeliv] = useState(null);
  const [who, setWho] = useState("sdek");
  useEffect(() => {
    console.log("Изменились рассчетные данные");
    if (activeCity !== null) getRange(activeCity.id);
  }, [activeCity, typeDelivery, order, activePVZ]);
  const getRange = () => {
    fetch("https://sahpossum.herokuapp.com/items/getRange", {
      method: "POST", // *GET, POST, PUT, DELETE, etc.
      mode: "cors", // no-cors, cors, *same-origin
      cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
      credentials: "same-origin", // include, *same-origin, omit
      headers: {
        "Content-Type": "application/json",
        // 'Content-Type': 'application/x-www-form-urlencoded',
      },
      redirect: "follow", // manual, *follow, error
      referrer: "no-referrer", // no-referrer, *client
      body: JSON.stringify({
        cityCode: activeCity.id,
        weight: String(order.meta.weight),
        count: order.meta.count,
        typeDelivery:
          activePVZ !== null ? (activePVZ.id === 123 ? "137" : "136") : "137",
        postal: activeCity.postal,
      }), // тип данны
    })
      .then((res) => res.json())
      .then((res) => {
        console.log(res);
        setDeliv(res);
      })
      .catch((e) => {
        console.log(e);
      });
  };

  if (!order.meta || order.meta.count === 0) {
    return (
      <Panel id={id}>
        <PanelHeader left={<PanelHeaderBack onClick={goBack} />} />
        <Placeholder
          stretched
          icon={<Icon56ArticleOutline />}
          header={"Ваша корзина пуста"}
          action={
            <Button onClick={goBack} mode={"outline"} size="l">
              Продолжить покупки
            </Button>
          }
        />
        <Footer />
      </Panel>
    );
  } else {
    return (
      <Panel id={id}>
        <PanelHeader left={<PanelHeaderBack onClick={() => go("home")} />} />
        <CartListItems
          changeCount={changeCount}
          order={order}
          onDeletePosition={onDeletePosition}
          onDecrementPosition={onDecrementPosition}
          onIncrementPosition={onIncrementPosition}
          clearCart={clearCart}
        />
        <Fragment>
          <Div>
            <MiniInfoCell
              after={<Counter>{order.meta ? order.meta.count : 0}</Counter>}
            >
              Товаров (штуки)
            </MiniInfoCell>
            <MiniInfoCell
              after={<Counter>{order.meta ? order.meta.sum : 0}</Counter>}
            >
              Сумма (рубли)
            </MiniInfoCell>
          </Div>
          <Group header={<Header mode="secondary">Доставка</Header>}>
            <Cell
              expandable
              onClick={() => go("GetCityList")}
              indicator={activeCity !== null ? activeCity.name : "Не выбрано"}
            >
              Город
            </Cell>
            {deliv !== null && (
              <Div>
                <Tabs>
                  <TabsItem
                    onClick={() => setWho("sdek")}
                    selected={who === "sdek"}
                  >
                    СДЭК
                  </TabsItem>
                  <TabsItem
                    onClick={() => setWho("PR")}
                    selected={who === "PR"}
                  >
                    Почта России
                  </TabsItem>
                </Tabs>
                {who === "sdek" ? (
                  <Delivery
                      activeCity={activeCity}
                    who={who}
                    fetchedUser={fetchedUser}
                    order={order}
                    typeDelivery={typeDelivery}
                    go={go}
                    activePVZ={activePVZ}
                    deliv={deliv.sdek}
                    sum={order.meta.sum}
                  />
                ) : (
                  <Delivery
                      activeCity={activeCity}
                    who={who}
                    fetchedUser={fetchedUser}
                    order={order}
                    typeDelivery={typeDelivery}
                    go={go}
                    activePVZ={activePVZ}
                    deliv={deliv.PR}
                    sum={order.meta.sum}
                  />
                )}
              </Div>
            )}
          </Group>
        </Fragment>
        <Footer />
      </Panel>
    );
  }
};

export default Cart;
